const Footer = () => {
    const style = {
        color: "#fff",
        backgroundColor: "#000",
        padding: "24px 16px",
        fontSize: "14px",
        lineHeight: "1.6",
    };

    const companyStyle = {
        display: "flex",
        flexDirection: "column",
        gap: "4px",
        alignItems: "center",
    };

    return (
        <footer style={style}>
            <div style={companyStyle}>
                <div>&nbsp;&nbsp;</div>
                <div> &nbsp;&nbsp;&nbsp;150-81-02170&nbsp;&nbsp;&nbsp;   253, 402 ()</div>
                <div>Copyright  Allright Reserved.</div>
            </div>
        </footer>
    );
};

export default Footer;