const Sort = ({ value="", onChange }) => {
    const style = {
        padding: "10px",
        marginLeft: "10px",
        borderRadius: "4px",
        border: "1px solid #ccc",
        width: "150px"
    };

    return (
        <select 
            value={value} 
            onChange={(e) => { 
                onChange && onChange(e.target.value); 
            }} 
            style={style}
        >
            <option value=""> </option>
            <option value="low"> </option>
            <option value="high"> </option>
            <option value="name"></option>
        </select>
    );
};

export default Sort;