import { configureStore, createSlice } from '@reduxjs/toolkit'

// 'user'   
let user = createSlice({
    name : 'user', 
    initialState : { name : ''}, //   
    reducers :{ 
        changeName(state){ //  ''  
            state.name = '' 
        },

    //     
    increase(state, action){ 
        state.age += action.payload} 
    } 
}
)

//        
export let { changeName, increase } = user.actions

// 'cart'    ()
let cart = createSlice({ 
    name : 'cart', 
    initialState : [ 
        {id : 201, imgurl: "set_menu/201.jpg", name : '  ', count : 2, price: 12900}, 
        {id : 302, imgurl: "one_menu/302.jpg", name : '(10P)', count : 1, price: 19000}, 
        {id : 510, imgurl: "sashimi_menu/507.jpg", name : '(16p)', count : 1, price: 25000} 
    ], 
    reducers : { 
        //   1 
    addCount(state, action) {
        let num = state.findIndex((a) => { 
            return a.id === action.payload;
        }); 

        state[num].count++; 
    }, 
        
//   1  (0   )
    decreaseCount(state, action) {
    let num = state.findIndex((a) => {
        return a.id === action.payload;
    });
    if (state[num].count > 1) {
        state[num].count--;
    } else if (state[num].count === 1) {
        alert(" .");
    }
    },
    //   ,    +1,   
    addItem(state, action) {
    let num = state.findIndex((a) => a.id === action.payload.id);
    if (num !== -1) {
        state[num].count++;
    } else {
        state.push(action.payload);
    }
    },
//   
    deleteItem(state, action) {
    let num = state.findIndex((a) => {
        return a.id === action.payload;
    });
    state.splice(num, 1);
    },

    //   
    sortName(state, action) {
    state.sort((a, b) => (a.name > b.name ? 1 : -1));
        },
        
    //   
    sortPrice(state, action) {
    state.sort((a, b) => a.price - b.price);
        }
    }
})

// cart      
export let { addCount, decreaseCount, addItem, deleteItem, sortName, sortPrice } = cart.actions;

//  Redux  
export default configureStore({
    reducer: {
        user: user.reducer,
        cart: cart.reducer,
    },
});


